import * as TREE from "./tree";
// import * as SUN from './sun';
import * as D3BAR from "./d3bar";
import * as d3 from "d3";
import '../css/reset.css';
import '../css/stylesheet.css';
import '../css/modal.css';

document.addEventListener('DOMContentLoaded', () => {
    // Audio setup
    const audioElement = document.getElementById("audioElement");
    audioElement.src = '/assets/audio/thismustbetheplace.mp3';
    audioElement.crossOrigin = "anonymous";
    
    let audioCtx = null;
    let audioSrc = null;
    let analyser = null;

    const initializeAudio = () => {
        if (!audioCtx) {
            audioCtx = new (window.AudioContext || window.webkitAudioContext)();
            audioSrc = audioCtx.createMediaElementSource(audioElement);
            analyser = audioCtx.createAnalyser();
            analyser.smoothingTimeConstant = 0.5;
            analyser.fftSize = 256;

            audioSrc.connect(analyser);
            audioSrc.connect(audioCtx.destination);
        }
    };

    const frequencyData = new Uint8Array(256);

    // Audio controls
    const playButton = document.querySelector('.play-button');
    playButton.addEventListener('click', () => {
        initializeAudio();
        audioElement.play();
    });

    const pauseButton = document.querySelector('.pause-button');
    pauseButton.addEventListener('click', () => audioElement.pause());

    const volBarSlider = document.querySelector('.vol-bar-slider');
    volBarSlider.addEventListener('input', e => {
        audioElement.volume = e.currentTarget.value / 100;
    });

    // Modal controls
    const closeButton = document.getElementsByClassName('close')[0];
    const modal = document.getElementsByClassName('modal')[0];
    const question = document.getElementsByClassName('help')[0];

    const closeModal = () => {
        modal.classList.add("closed");
        initializeAudio();
        audioElement.play();
    };

    modal.addEventListener("click", closeModal);
    closeButton.addEventListener("click", closeModal);
    question.addEventListener("click", () => modal.classList.remove("closed"));

    // Canvas setup
    const canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');
    const treeCanvas = document.getElementById('treeCanvas');
    const treeContext = treeCanvas.getContext('2d');

    const resizeCanvases = () => {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        treeCanvas.width = window.innerWidth;
        treeCanvas.height = window.innerHeight;
    };

    window.addEventListener('resize', resizeCanvases);
    resizeCanvases();

    // Sun animation
    function drawSun(context, frequencyDataPoint) {
        const x = canvas.width * 0.90;
        const y = 200;
        const hue = 40;
        const transparency = 0.08;

        context.beginPath();
        context.arc(x, y, (Math.abs(frequencyDataPoint-100)) * 2, 0, 2 * Math.PI);
        context.strokeStyle = `hsla(${hue}, 100%, 40%, ${transparency})`;
        context.fillStyle = `hsla(${hue}, 100%, 60%, 0.01)`;
        context.lineWidth = 2;

        context.fill();
        context.stroke();
    }

    function animateSun(ctx, canvasWidth, canvasHeight, analyser) {
        if (!analyser) return;
        
        ctx.clearRect(0, 0, canvasWidth, canvasHeight);
        analyser.getByteTimeDomainData(frequencyData);

        for (let i = 0; i < frequencyData.length; i += 1) {
            drawSun(context, frequencyData[i]);
        }

        requestAnimationFrame(() => animateSun(ctx, canvasWidth, canvasHeight, analyser));
    }

    animateSun(context, canvas.width, canvas.height, analyser);

    // Tree visualization
    TREE.drawTree(
        treeCanvas.width/2,
        treeCanvas.height,
        150,
        0,
        20,
        treeContext,
        treeCanvas,
        "black",
        "red",
        "pink"
    );

    // D3 Bar chart
    const svgHeight = window.innerHeight;
    const svgWidth = window.innerWidth + 1700;
    const barPadding = 15;

    const svg = D3BAR.createSvg('.frequency-bar', svgHeight, svgWidth);

    svg.selectAll('rect')
        .data(frequencyData)
        .enter()
        .append('rect')
        .attr('x', (d, i) => i * (svgWidth / frequencyData.length))
        .attr('width', svgWidth / frequencyData.length - barPadding);

    function renderChart() {
        if (!analyser) return;
        
        requestAnimationFrame(renderChart);

        analyser.getByteFrequencyData(frequencyData);

        svg.selectAll('rect')
            .data(frequencyData)
            .attr('y', d => svgHeight - d)
            .attr('height', d => d)
            .attr('fill', d => `rgb(0, 200, ${d})`);
    }

    renderChart();
});
