  position: absolute;
  width: 100%;
  background-color: rgba(255,255,255, .6);
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1000;
}

.opening {
  border: 3px solid white;
  height: 700px;
  width: 400px;
  margin: 0 auto;
  padding: 25px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-color: #e6f7ff;
  background-size: cover;
  color: black;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: 20px;
  font-style:oblique;
}

.professional-links {
  justify-content: center;
  display: flex;
}

.opening .text{
  text-align: center;
}

.opening h1 {
  font-size: 30px;
  margin: 0 auto;
  text-align: center;
  border-bottom: 1px solid #ccc;
  padding-left: -20px;
  padding-right: -20px;
}

.opening li {
  display: inline-block;
  margin: 0 6px;
}

.opening .buttons {
  margin: 0 auto;
}
.opening a {
  margin-top: 30px;
  padding: 5px 10px;
  margin: 5px;
  display: block;
}

.opening a:hover {
  font-weight: bold;
}

.opening a:active {
  transform: scale(1.1);
}

.close {
  width: 93%;
  text-align: center;
}

.close a {
  text-align: center;
}

.close:hover {
  font-weight: bold;
}

.close:active {
  transform: scale(1.1);
}

.closed {
  display: none;
}
